@import url(https://fonts.googleapis.com/css?family=Nunito);
:root
{
    --fondo2: #130f40;
    --principal: #4834d4;
    --secundario: #eb4d4b;
    --font: #dff9fb;
    --font2: #535c68;
    --rojo: #ff7979;
    --verde: #badc58;
    --delay: 0s;
}

body 
{
    margin: 0;
    font-family: 'Nunito';
    color: #130f40;
    color: var(--fondo2);
    width: 100%;
    height: 100vh; 
}

header
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 7px 20px;
}

header h3
{
    margin: 0;
}

.container-app
{
    display: flex;
    justify-content: center;
    height: calc(100vh - 70px);
    align-items: center;
}

.container-info-sistem
{
    position: absolute;
    color: white;
    display: flex;
    background-color: #130f40cf;
}

.container-bienvenida
{
	max-width: 972px;
}

.container-info
{
    display: flex;
    justify-content: center;
}

.container-max
{
    margin-top: 100px;
    max-width: 972px;
}

.container-habilidades
{
    height: 450px;
}

.container-scroll-mouse
{
    position: relative;
    text-align: center;
    bottom: -130px;
}

.container-mouse
{
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.container-redes
{
    display: flex;
    justify-content: center;
    grid-gap: 49px;
    gap: 49px;
}

.container-texto-bienvenida
{
	margin-bottom: 20px;
}

.container-img-bienvenida
{
	display: flex;
}

.container-text-img
{
	display: flex;
	align-items: end;
}

h1
{
    font-size: 50px;
    color: #4834d4;
    color: var(--principal);
}

h2
{
    margin: 0;
    font-size: 40px;
    color: #4834d4;
    color: var(--principal);
    text-align: center;
}

h3
{
    color: #dff9fb;
    color: var(--font);
    margin-top: 0;
	margin-bottom: 19px;
}

p
{
    font-size: 26px;
    margin-top: 60px;
}

.img-mi-foto
{
    border-radius: 50%;
    border: 7px solid #4834d4;
    border: 7px solid var(--principal);
    /* position: absolute; */
    min-width: 200px;
	height: 200px;
}

.btn-mas-info-sist
{
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.titulo-bienvenida
{
    width: 590px;
    -webkit-animation: escribir 2s steps(26), parpadeo .5s step-end infinite alternate;
            animation: escribir 2s steps(26), parpadeo .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
}

.scroll-text
{
	color: #535c68;
	color: var(--font2);
	-webkit-animation: colorText2 5s ease-out infinite, nudgeText 5s ease-out infinite;
	        animation: colorText2 5s ease-out infinite, nudgeText 5s ease-out infinite;
}

.mouse 
{
	background: #4834d4 linear-gradient(transparent 0%, transparent 50%, #fff 50%, #fff 100%);
	background: var(--principal) linear-gradient(transparent 0%, transparent 50%, #fff 50%, #fff 100%);
	position: relative;
	width: 52px;
	height: 88px;
	border-radius: 100px;
	background-size: 100% 200%;
	-webkit-animation: colorSlide 5s linear infinite, nudgeMouse 5s ease-out infinite;
	        animation: colorSlide 5s linear infinite, nudgeMouse 5s ease-out infinite;
}

.mouse:before, .mouse:after 
{
	content: "";
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
}

.mouse:before 
{
	width: 46px;
	height: 82px;
	background-color: white;
	border-radius: 100px;
}

.mouse:after 
{
	background-color: #4834d4;
	background-color: var(--principal);
	width: 10px;
	height: 10px; 
	border-radius: 100%;
	-webkit-animation: trackBallSlide 5s linear infinite;
	        animation: trackBallSlide 5s linear infinite;
}

.link-cont:hover
{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: .2s ease all;
}

.text-bienvenida
{
	margin: 0;
}

@-webkit-keyframes top 
{
    0% 
    {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
    }
    
    100% 
    {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}

@keyframes top 
{
    0% 
    {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
    }
    
    100% 
    {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}

@-webkit-keyframes escribir 
{
    from 
    {
        width: 0;
    }
}

@keyframes escribir 
{
    from 
    {
        width: 0;
    }
}
      
@-webkit-keyframes parpadeo 
{
    50% 
    {
      border-color: transparent;
    }
}
      
@keyframes parpadeo 
{
    50% 
    {
      border-color: transparent;
    }
}

